.main-content {
  margin-left: 160px;
}

div.Index div.card {
  width: 20%;
  float: left;
}
div.Index div.card:hover {
  background-color: #dadada;
  cursor: pointer;
}

div.Index div.card:hover .list-group-item {
  background-color: #dadada;
}

div.Index div.card button {
  height: 40px;
}

.Sidebar {
  height: 100%;
  width: 160px;
  position: fixed;
  z-index: 1;
  background-color: #111;
  overflow-x: hidden;

  color: #f1f1f1;
}

